.quora-box {
    display: flex;
    flex-direction: column;
    padding: 10px;
    border: 1px solid lightgray;
    background-color: white;
    border-radius: 5px;
    cursor: pointer;
    margin-right: 140px;
}

.quora-box:hover{
    border: 1px solid gray;
}
.quorabox-info {
    display: flex;
    align-items: center;
}

.quorabox-info > h4 {
    margin-left: 10px;
} 


.quorabox-quote {
      display:flex;
      margin-top: 8px;
}

.quorabox-quote > p {
    font-size: large;
    font-weight: bold;
    color: #008eff;
}
