.sideopt{
    display: flex;
    flex-direction:column;
    position:fixed;
}

.image{
    display: flex;
    align-items: center;
    padding: 10px;
    cursor: pointer;
}
.image:hover{
    background-color: lightgray;
}


.image > h5 {
    color:black;
    margin-left: 10px;
    border-radius: 500px;
    
}

.image > .MuiSvgIcon-root {
    color: #008eff;
    font-size:xx-large;
    margin-left: 20px;
}