*{
    margin: 0;
}

.qheader{
    display: flex;
    justify-content:center;
    align-items: center;
    padding: 1px;
    background-color: white;
    width: 100%;

    position: sticky;
    z-index: 1000;
    top: 0;
    box-shadow: 0px 5px 8px -9px rgba(0,0,0,0.5); 
}

.qheader-logo  {
    display: flex;
    flex: 0.3;

}


.qheader-logo > img {
    margin: -8px 0px -4px 13px  ;
    
    
}

.qheader-icons{
    display: flex;
    align-items: center;
    flex: 0.5;
}

.qheader-icon > img {
         display: flex;
         align-items: center;
         margin: 5px 35px ;
}

.qheader-search{
    display: flex;
     align-items: center;
     border: 1px solid lightgray;
     padding: 5px;
     border-radius: 20px;
     margin: 8px;
}

.qheader-search > input {
    background-color: transparent;
    outline: none;
    border: none;
    color: rgb(49,49 ,49 );
}

.qheader-search > .MuiSvgIcon-root{
    color:#008eff; 
}

.qheader-rem{
    display: flex;
    flex: 0.2;
    align-items: center;
}

.qheader-rem > img {
    display: flex;
    margin: 5px;
}
.qheader-rem > button {
    color:white;
    border: none;
    outline: none;
    text-transform: inherit;
    background: #008eff;
    border-radius: 20px;
    margin-left: 10px;
    cursor: pointer;
    padding: 10px;
}

